import { useNavigate } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";

import "../css/homeComp.css";

const HomeComp = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="home-section1">
        <img
          className="home-section1-img"
          src="https://guswns951230.github.io/React-project/img/homepage1.jpg"
          alt="home-image-1"
        />
        <h1>     &nbsp;  </h1>
        <p>
            <strong></strong> 1  
          <br />
              1:1   .
          <br />
          <br />
               
          <br />
            .
        </p>
      </div>

      <div className="home-section2">
        <p>
          <span className="home-section2-title"> PROFESSIONAL </span>
          <br />
          <span className="home-section2-text">  </span>
        </p>
        <img
          className="home-section2-img"
          src="https://guswns951230.github.io/React-project/img/homepage2.jpg"
          alt="home-image-2"
        />
      </div>

      <div className="home-section-event">
        <img
          className="home-section-event-img"
          src="https://guswns951230.github.io/React-project/img/homepage3.jpg"
          alt="home-image-3"
        />
        <div className="home-section-event-box">
          <p className="home-section-event-title"> EVENT </p>
          <p className="home-section-event-text">
             Event   !
          </p>
          <button
            onClick={() => {
              navigate("/event");
            }}
            className="eventBtn"
          >
            <FontAwesomeIcon icon={faPlus} />
            &nbsp;&nbsp;&nbsp;View Event
          </button>
        </div>
      </div>
    </>
  );
};

export default HomeComp;
